::placeholder {
  color: #fff;
  opacity: .3;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

.col {
  float: left;
  width: 50%;
}

*, *::after, *::before {
  -webkit-user-drag: none;
  -webkit-app-region: no-drag;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: #fff;
  text-align: center;
  padding: 0 40px;
}

body header {
  height: 30px;
  -webkit-app-region: drag;
}

body main {
  padding-bottom: 20px;
}

body main figure {
  width: 128px;
  height: 128px;
  margin: 10px auto 20px;
}

body main figure img {
  display: block;
}

body main h1 {
  font-size: 14px;
  margin-bottom: 10px;
}

body main h1 #version {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  font-weight: 200;
  padding-left: 10px;
}

body main p {
  font-size: 11px;
  font-weight: 200;
  color: rgba(255, 255, 255, 0.6);
}

body footer table {
  margin: 0 auto;
  font-size: 10px;
  line-height: 14px;
  font-weight: 200;
  color: rgba(255, 255, 255, 0.4);
}

body footer table th,
body footer table td {
  padding: 0 4px;
}

body footer table th {
  text-align: right;
}

body footer table td {
  text-align: left;
}

body footer table tfoot button {
  display: block;
  width: auto;
  height: 20px;
  line-height: 11px;
  margin: 10px auto 0;
  background: #031320;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 2px;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  font-weight: 200;
  text-align: center;
  transition: all .15s ease-in-out;
}

body footer table tfoot button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

body footer table tfoot button:active {
  background-color: #020b12;
  box-shadow: inset 0 2px 4px #000204;
  outline: none;
}
